import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import { Dialog, Button, Form, FormItem, Input } from 'element-ui'
Vue.use(Dialog)
Vue.use(Button)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
